///<reference path="../../index.d.ts"/>
///<reference path="../types/raphael.ts"/>
///<reference path="../types/jquery.ts"/>

// declare var $: jQuery
// declare var $: jquery
    // BUG[180331]  <reference path="../types/jquery.ts"/> 
declare var $: any


/**
 * @export
 * @abstract
 * @class NodeAbstract
 */
export default abstract class NodeAbstract{
    conLns: {
        from?: string[]    // [{code: '', posi: ''}] 
        to?: string[]
    }
    fromLine: rSu.Node[]    // 
    toLine: rSu.Node[]      // 
    NodeType: string            // 
    opt: rSu.NodeOpt            // 
    label?: RaphaelElement      // 
    c?: RaphaelElement          // 
    paper: RaphaelPaper         // Raphael 
    isSelEd: boolean            // 

    // 
    xRate?: number           // 
    inlinesEle?: RaphaelElement[]    // 
    inlineEle?: RaphaelElement // 
    tRElem: rSu.MapRElm        // 
    protected _dataQueueDick: rSu.bsMap
    protected _code: string

    constructor(paper: RaphaelPaper, opt?: rSu.NodeOpt){
        this.conLns = {
            from: [],
            to: []
        }
        this.tRElem = {}
        this._dataQueueDick = {}
        this.isSelEd = false
        this.paper = paper
        // 
        this.fromLine = []
        this.toLine = []
        this.NodeType = null            // 
        // 
        if(opt){
            opt.bkgMagnetic = opt.bkgMagnetic || '#FF0000'
            this.opt = opt
        }        
        this._onInit()
    }
    /**
     * @param {string|number} key _code 
     * @param {*} value 
     */
    data(key?:any, value?:any): any{        
        if('undefined' == typeof value){
            if('undefined' == typeof key){
                return Util.clone(this._dataQueueDick)
            }
            return this._dataQueueDick[key]
        }else{
            this._dataQueueDick[key] = value
            //  code 
            if(!this._code && key == '_code'){
                this._code = value
            }
        }
        return this
    }
    /**
     * 
     */
    get code():string{
        return this._code
    }
    /**
     * 
     * @param opt 
     */
    creator(opt?: rSu.NodeOpt){
        this._whenCreatorEvt()
        return this
    }
    /**
     * path
     * @param {array} pQue 
     * @param {bool} isClose 
     * @returns {string}
     */
    protected _ps2Path(pQue: rSu.P[], isClose?: boolean): string{
        var path = ''
        for(var i=0; i<pQue.length; i++){
            path += (path? 'L': 'M') + pQue[i].x + ',' + pQue[i].y
        }
        if(isClose){
            path += 'Z'
        }        
        return path
    }
    /**
     * 
     * @param {array} pQue 
     * @param {bool} isClose 
     * @returns {string}
     */
    protected _ps2PathAttr(pQue: rSu.P[], isClose?: boolean){
        var pArr: any[] = []
        for(var i=0; i<pQue.length; i++){
            var cPArr: any[] = ['L']
            if(pArr.length == 0){
                cPArr[0] = 'M'
            }
            cPArr.push(pQue[i].x, pQue[i].y)
            pArr.push(cPArr)
        }
        if(isClose){
            pArr.push(['Z'])
        }        
        return pArr
    }
    /**
     * ()
     * @param value 
     * @param isEnd 
     */
    line(value: string, isEnd?: boolean): rSu.Node{
        if(isEnd){
            this.conLns.to.push(value)
        }
        else{
            this.conLns.from.push(value)
        }
        return <rSu.Node>this
    }
    /**
     * 
     */
    protected _whenCreatorEvt(){}
    /**
     *  []
     */
    protected _onInit(){}
    /**
     *  []
     */
    onDrag(){}
    /**
     * 
     * @param {stirng} type 
     * @param {this}  $node 
     */
    recordLine(type: string, $node: any){
        if('from' == type){
            this.fromLine.push($node)
        }
        else if('to' == type){
            this.toLine.push($node)
        }
    }
    /**
     * 
     * @param {function} callback 
     */
    syncLineMove(callback: any){
        if('function' !== typeof callback){
            callback = (instance: any, type: any) => {}
        }
        // 
        var fLines = this.fromLine        
        var tLines = this.toLine
        // 
        for(var i=0; i<fLines.length; i++){
            var $fC = fLines[i].c
            var $fPath = $fC.attr('path')
            callback($fC, 'from', fLines[i])
        }
        // 
        for(var j=0; j<tLines.length; j++){
            var $tC = tLines[j].c
            callback($tC, 'to', tLines[j])
        }
    }
    /**
     * 
     * NodeBase struct to json   struct 
     * @returns {object}
     */
    toJson(){
        var _struct: Flower.StepStru = {
            NodeType: this.NodeType,            // 
            opt: this.opt,                      // 
            c: {
                attr: this.c.attr()              // 
            }            
        }
        if(this.label){                         // 
            _struct.label = {
                attr: this.label.attr()
            }
        }
        return _struct
    }
    /**
     * 
     */
    getPLen(P1: rSu.P, P2: rSu.P): number{
        return Math.pow(
            (Math.pow((P1.x - P2.x), 2) + Math.pow((P1.y - P2.y), 2)),
            1/2
        )
    }
    /**
     * 
     * @param nOpt 
     */
    protected _updAttr(nOpt: rSu.NodeOpt){
        for(var key in nOpt){
            if('undefined' != typeof this.opt[key]){
                this.opt[key] = nOpt[key]
            }
        }
        return this
    }
    /**
     * 
     */
    protected _getTextPnt(): rSu.P{
        let {cx, cy} = this.opt,
            p = {x: cx + 1, y: cy + 1}
        return p
    }
    /**
     * 
     */
    delete(){        
        this.c.remove()
        if(this.label){
            this.label.remove()
        }
        if(this.inlineEle){
            this.inlineEle.remove()
        }
        if(this.inlinesEle){
            this.inlinesEle.forEach((ist: RaphaelElement) => {
                ist.remove()
            })
        }
        this.removeBox()
    }
    /**
     * 
     */
    hide(){
        if(this.c){
            this.c.hide()
        }
        if(this.label){
            this.label.hide()
        }
        // 
        if(this.inlinesEle){
            this.inlinesEle.forEach((nd) => {
                nd.hide()
            })
        }
        if(this.inlineEle){
            this.inlineEle.hide()
        }
        return this
    }
    /**
     * 
     */
    show(){
        if(this.c){
            this.c.show()
        }
        if(this.label){
            this.label.show()
        }
        // 
        if(this.inlinesEle){
            this.inlinesEle.forEach((nd) => {
                nd.show()
            })
        }
        if(this.inlineEle){
            this.inlineEle.show()
        }
        return this
    }
    /**
     * 
     * @returns 
     * @memberof NodeAbstract
     */
    moveable(){
        var $this = this;
        this.c.undrag()
        var tP = {cx: 0, cy: 0}
        this.c.drag(
            function(dx: number, dy: number){
                dx += tP.cx
                dy += tP.cy
                $this.updAttr({cx: dx, cy: dy})
                $this.select()
            },
            function(){
                let {cx, cy} = $this.opt
                tP = {cx, cy}
            }
        )
        return $this
    }
    /**
     * 
     * @param nOpt 
     */
    updAttr(nOpt: rSu.NodeOpt): rSu.Node{
        return <rSu.Node>this
    }    
    /**
     * 
     */
    getBBox(): rSu.BoxAttr{
        let {x, y, width, height} = this.c.getBBox()
        x -= 3, y -= 3
        width += 6, height += 6

        //  
        let mx = x + width/2, 
            xx = x + width,
            my = y + height/2,
            xy = y + height,
            ps: rSu.bsMap = {
                a: {x, y},             // A
                b: {x: mx, y},         // B
                c: {x: xx, y},         // C
                d: {x: xx, y: my},     // D
                e: {x: xx, y: xy},     // E
                f: {x: mx, y: xy},     // F
                g: {x: x, y: xy},      // G
                h: {x: x, y: my}       // H
            }
        let attr = {x, y, width, height}
        return {attr, ps}
    }
    /**
     * 
     * @param px
     * @param py
     */
    magnCore(px: number, py: number): RaphaelElement{
        let bAttr = this.getBBox(),
            {attr, ps} = bAttr,
            {x, y} = attr,
            w = attr.width,
            h = attr.height
        // a-h
        let pt: rSu.P,
            cx1 = x + w/4,
            cx = x + w/2,
            cx2 = x + w*(3/4),
            cy1 = y + w/4,
            cy = y + w/2,
            cy2 = y + w*(3/4),
            posi = null

        if(px <= cx1 && py <= cy1){
            pt = ps.a
            posi = 'a'
        }
        else if((px > cx1 && px < cx2) && py <= cy1){
            pt = ps.b
            posi = 'b'
        }
        else if((px >= cx2) && py <= cy1){
            pt = ps.c
            posi = 'c'
        }
        else if((px >= cx2) && (py > cy1 && py < cy2)){
            pt = ps.d
            posi = 'd'
        }
        else if((px >= cx2) && py >= cy2){
            pt = ps.e
            posi = 'e'
        }
        else if((px > cx1 && px < cx2) && py >= cy2){
            pt = ps.f
            posi = 'f'
        }
        else if((px <= cx1) && py >= cy2){
            pt = ps.g
            posi = 'g'
        }
        else if((px<= cx1) && (py > cy1 && py < cy2)){
            pt = ps.h
            posi = 'h'
        }
        this.clearTmpElem('mc')
        if(pt){
            this.tRElem['mc'] = this.paper
                .circle(pt.x, pt.y, 3)
                .attr('fill', this.opt.bkgMagnetic)
                .data('pcode', this.code)
                .data('posi', posi)
        }
        let rElem: RaphaelElement
        if(this.tRElem['mc']){
            rElem = this.tRElem['mc']
        }
        return rElem
    }
    /**
     * 
     */    
    select(): rSu.Node{
        let selMk = false,
            bAttr = this.getBBox(),
            {attr, ps} = bAttr,
            {x, y, width, height} = attr,
            {paper} = this,
            ist:RaphaelElement 
        
        this.removeBox()
        this.isSelEd = true        
        this.tRElem['box'] = paper.rect(x, y, width, height)
            .attr({
                'stroke': '#0033FF',
                'stroke-width': '0.8'
            })        
        for(var key in ps){
            let {x, y} = ps[key]
            this.tRElem['__p' + key] = paper.circle(x, y, 2)
                .attr('fill', '#000000')
                .data('pcode', this.code)
                .data('posi', key)
            this.onCreateBoxPnt(this.tRElem['__p' + key])            
        }
        return <rSu.Node>this
    }
    /**
     * 
     */
    removeBox(){
        if(!this.tRElem){
            this.tRElem = {}
        }
        // 
        if(this.tRElem.box){
            this.tRElem.box.remove()
            delete this.tRElem.box
        }
        for(var key in this.tRElem){
            if(key.indexOf('__p') > -1){
                this.tRElem[key].remove()
                delete this.tRElem[key]
            }
        }
        return this
    }
    /**
     * 
     * @param {number} rate  0.05 [0-1]
     */
    zoomOut(rate?: number): rSu.Node{
        rate = rate? rate: 0.05
        var {c, opt} = this
        opt.w = opt.w * (1 + rate)
        opt.h = opt.h * (1 + rate)
        this.updAttr({
            w: opt.w,
            h: opt.h
        })
        this.select()
        return <rSu.Node>this
    }
    /**
     * 
     * @param {number} rate  0.05 [0-1]
     */
    zoomIn(rate?: number): rSu.Node{
        rate = rate? rate: 0.05
        var {c, opt} = this
        opt.w = opt.w * (1 - rate)
        opt.h = opt.h * (1 - rate)
        this.updAttr({
            w: opt.w,
            h: opt.h
        })
        this.select()
        return <rSu.Node>this
    }
    /**
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move(type?: string, rate?: number): rSu.Node{
        rate = rate? rate: 0.05
        var {opt} = this,
            uOpt: rSu.NodeOpt
        type = type? type.toUpperCase() : type
        switch(type){
            case 'T':
                uOpt = {cy: opt.cy * (1 - rate)}
                break
            case 'B':
                uOpt = {cy: opt.cy * (1 + rate)}
                break
            case 'L':
                uOpt = {cx: opt.cx * (1 - rate)}
                break
            case 'R':
                uOpt = {cx: opt.cx * (1 + rate)}
                break
        }
        if(uOpt){
            this.updAttr(uOpt)
            this.select()
        }
        return <rSu.Node>this
    }
    /**
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move2T(rate?: number): rSu.Node{
        this.move('T', rate)
        return <rSu.Node>this
    }
    /**
     * 
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move2B(rate?: number): rSu.Node{
        this.move('B', rate)
        return <rSu.Node>this
    }
    /**
     * 
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move2L(rate?: number): rSu.Node{
        this.move('L', rate)
        return <rSu.Node>this
    }
    /**
     * 
     * 
     * @param {number} rate  0.05 [0-1]
     */
    move2R(rate?: number): rSu.Node{
        this.move('R', rate)
        return <rSu.Node>this
    }
    /**
     * 
     * @param {string} type  
     */
    background(type?: string): rSu.Node{
        if(type){
            type = type.toLowerCase()
        }
        switch(type){
            case 'magn':        // 
                this.c.attr('fill', this.opt.bkgMagnetic)
                break
            default:
                this.c.attr('fill', this.opt.bkg)
        }
        return <rSu.Node>this
    }
    /**
     * 
     * @param key 
     */
    clearTmpElem(key?: string|Array<string>): rSu.Node{
        if(key){
            let tArr: Array<string> = []
            if('object' == typeof key){
                tArr = key
            }else{
                tArr = [key]
            }
            Util.each(this.tRElem, (k: string, elem: RaphaelElement) => {
                if($.inArray(k, tArr) > -1){
                    elem.remove()
                    delete this.tRElem[k]
                }
            })            
        }else{
            Util.each(this.tRElem, (k: string, elem: RaphaelElement) => {
                elem.remove()
                delete this.tRElem[k]
            })  
        }
        return <rSu.Node>this
    }
    /**
     *  [] 
     */
    onCreateBoxPnt(rElem: RaphaelElement){}
}


